.skills-list {
    list-style: none;
    padding: 0;
  }
  
  .skills-list li {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .skills-list li span {
    width: 100px;
    color: #1a73e8;
    font-weight: bold;
  }
  
  .progress-bar {
    width: 240px;
    height: 20px;
    background-color: #d3d3d3;
  }
  
  .progress-fill {
    height: 100%;
    background-color: #1a73e8;
  }